import * as React from "react"
import { Carousel } from "react-bootstrap"

import "/src/assets/css/components/hexagons.css"

var currentLogoIndex = 0

function HexDisplay(props) {
  currentLogoIndex = 0
  return (
    <>
      {(() => {
        if (props.isMobile === "true")
          return mobileHexLayout(
            props.layout.colSizeMobile,
            props.layout.mobileLayout,
            props.content,
            props.layout.customColClasses
          )
        return hexLayout(
          props.layout.colSize,
          props.layout.layout,
          props.content,
          props.layout.customColClasses
        )
      })()}
    </>
  )
}

function hexLayout(colSize, layout, content, customColClasses) {
  var colSizePercentageSeparation = (100 / 12) * colSize * 0.25
  return (
    <>
      {layout.map((row, rowIndex) => (
        <div
          key={"row-" + rowIndex.toString()}
          className={`row hex-row justify-content-center m-0 ${
            rowIndex % 2 == 1 ? "no-height" : ""
          } ${layout.length - 1 == rowIndex ? "last-row" : ""}`}
        >
          {row.map((cellValidity, colIndex) => {
            if (cellValidity == "0")
              return (
                <div
                  key={
                    "row-" + rowIndex.toString() + "col-" + colIndex.toString()
                  }
                  className={`col-${colSize} hex-col`}
                  style={{
                    marginLeft: `${colSizePercentageSeparation}%`,
                    marginRight: `${colSizePercentageSeparation}%`,
                  }}
                ></div>
              )

            var values = undefined

            try {
              values = content().contents[currentLogoIndex]
            } catch (error) {}

            if (cellValidity == "1" || values == undefined)
              return (
                <div
                  key={
                    "row-" + rowIndex.toString() + "col-" + colIndex.toString()
                  }
                  className={`col-${colSize} hex-col`}
                  style={{
                    marginLeft: `${colSizePercentageSeparation}%`,
                    marginRight: `${colSizePercentageSeparation}%`,
                  }}
                >
                  <div className={`hexagon ${customColClasses}`}></div>
                </div>
              )

            var result = (
              <div
                key={
                  "row-" + rowIndex.toString() + "col-" + colIndex.toString()
                }
                className={`col-${colSize} hex-col`}
                style={{
                  marginLeft: `${colSizePercentageSeparation}%`,
                  marginRight: `${colSizePercentageSeparation}%`,
                }}
              >
                <div
                  className={`hexagon ${customColClasses} ${values.hexClasses}`}
                >
                  <img
                    src={values.path}
                    alt={values.alt}
                    width={values.width}
                  />
                </div>
              </div>
            )

            currentLogoIndex++
            return result
          })}
        </div>
      ))}
    </>
  )
}

function mobileHexLayout(colSize, layout, content, customColClasses) {
  return (
    <Carousel slide={false}>
      {layout.map((row, index) => {
        return (
          <Carousel.Item key={"ci-" + index.toString()} interval={2500}>
            {hexLayout(colSize, row, content, customColClasses)}
          </Carousel.Item>
        )
      })}
    </Carousel>
  )
}

export default HexDisplay
