export default Index

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(
      filter: { ns: { in: ["global", "index"] }, language: { eq: $language } }
    ) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`
